import React from 'react';
import {Grid, Button} from '@mui/material';

const DefaultPage = () => {
	return (
		<>
			<Grid container spacing={2}>
				<Grid item xs={4} ml={5} mt={25}>
					<span className='span-custom'>Welcome To iCare</span>
					<h1 style={{marginTop: 0}}>Bring Safety Care To Your Kids</h1>
					<p>Amazing Playground for your kids</p>
					<Button className='btn-custom' variant='contained' size='large' style={{cursor: 'pointer'}}>
						Learn More
					</Button>
				</Grid>
				<Grid item xs={7}>
					<img className='main-img' src='/kid_transparent.png' alt='kid' height='800px' />
				</Grid>
			</Grid>
		</>
	);
};

export default DefaultPage;
